import {mapState} from 'vuex'
import ActivityInfoRow from './ActivityInfoRow'
// import { FullCalendar } from 'vue-full-calendar'
import _ from 'lodash'
export default {
    data(){
      return {
        order: true
      }
    },
    mounted() {
        console.log('Component mounted.')
       $('#calendar').fullCalendar({
       })
    },
    computed:{
      ...mapState({
        user: state=>state.auth.user,
        activities: 'activities'
      }),
      OrderedList(){
        let result = this.activities.slice().sort((a,b)=>new Date(a.time)>new Date(b.time) )
        
        if (this.order){
          result=result.reverse()
        }
        console.log(result.map(o=>o.time))
        return result
      },
      chunkedList(){
        //依照年分跟月份分群
        let result =  _.groupBy(
          this.OrderedList,event=>
            (new Date(event.time.replace(/-/g, "/") )).getFullYear()*100+ ((new Date(event.time.replace(/-/g, "/") )).getMonth()+1)
        )
        console.log(result)

        //轉換成陣列進行排序
        let resultArray = []
        let keys = Object.keys(result)
        if (keys){
          keys.forEach(key => {
                  console.log(key)
                  resultArray.push( {
                  'time':key, 
                  'events':result[key]
                  })
          })
          
        }
        if (this.order){
          resultArray=resultArray.reverse()
        }
        return resultArray
      }
    },
    components:{
      ActivityInfoRow,
      // FullCalendar
    },
    watch:{
      activities(){
        // $('#calendar').fullCalendar('renderEvent', {
        //     id: 'eventGroup1',
        //     title: '活動1',
        //     start: moment().add(3, 'days').format('YYYY-MM-DD'),
        //     textColor: 'black',
        //     color: 'beige'
        // });
      }
    }
}



