import {mapState} from 'vuex'
import ActivityInfoRow from './Activity/ActivityInfoRow'
export default {
    data(){
      return {
        registedActivityList: []
      }
    },
    mounted() {
        console.log('Component mounted.')
        axios.get("/api/user/activity").then(res=>{
          this.registedActivityList=res.data
        })
    },
    computed:{
      ...mapState(['user'])
    },
    components:{
      ActivityInfoRow
    }
}



